/*--------------------expanse__chat----------------------*/
.expanse__chat {
  padding: rem(10) rem(20);
  display: flex;
  // align-items: center;
  min-height: rem(60);
  border-radius: 0 0 0 rem(30);
  border-top: 1px solid $line;
  background-color: #fff;
  box-shadow: 0px 10px 15px 0px rgba(71, 93, 169, 0.04),0px -6px 15px 0px rgba(71, 93, 169, 0.03);

}
.chat-form {
  display: flex;
  align-items: center;
  flex: 1 1 auto;
  column-gap: rem(20);
}
.chat-form__label {
  position: relative;
}
.file-input {
  @include checkbox();

  .icon{
    @include absolute-center;
  }
}
.chat-form__file-add {
  width: rem(24);
  // height: rem(24);
  flex-shrink: 0;
  position: relative;

  @media (any-hover: hover) { //только для устройств с ховером
      cursor: pointer;
    }

  .icon {
    font-size: rem(20);
    color: $gray-dark;
  }

}
.chat-form__smile{
  position: relative;
  top: rem(-1);
  @include but-null();
  .icon {
    font-size: rem(20);
    color: $gray-dark;
  }
}
.icon-plus-circle {}
.chat-box {
  flex: 1 1 auto;
}
.chat-box__field {

}
.chat-form__send{
  flex: 0 0 rem(24);
  height: 100%;
  display: flex;
  align-items: center;
  @include but-null();

  .icon{
    font-size: rem(20);
  }
}
/*--------------------END expanse__chat----------------------*/ 
