// обертка для строки
.chat-box {
  // max-height: 1.5rem;
  // height: 1.5rem;
  cursor: text;
  position: relative;
  overflow: hidden;
  // padding: 0 .5rem;
  // line-height: 1.5rem;
  // background-color: $main-color;
  font-weight: 400;
  
  // поле ввода
  #input-box-field {
    
    display: block; // см. https://stackoverflow.com/questions/18552336/prevent-contenteditable-adding-div-on-enter-chrome
    // word-break: break-all;
    &:focus { outline: 0; }
    
    
    // position: absolute;
    // top: 0;
    // left: .5rem;
    // right: .5rem;
    // white-space: nowrap;
    // overflow-x: scroll; // скрытие скроллбара
    // min-height: 2.5rem;
    // resize: none;
    // text-overflow: ellipsis;
    max-height: rem(300);
    overflow: auto;
    
    // вывод placeholder
    &:not(:empty) ~ #input-box-placeholder { display: none; }
    &:empty { caret-color: darken($main-color,15%); }
    
    user-select: text;
    -webkit-touch-callout: text;
    -webkit-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    -o-user-select: text;
    
    // скрытие лишних тегов
    br, p, div { display: none; }

    // смайлики
    .emoji {
      cursor: text;
      vertical-align: middle;
      display: inline;
      max-width: $width;
      min-width: $width;
      max-height: $width;
      min-height: $width;
    }
    
  }
  
  // placeholder
  // #input-box-placeholder {
  //   position: absolute;
  //   top: 0;
  //   left: .5rem;
  //   right: .5rem;
